
.Header__Menu ul {
    display: flex;
    padding: 0;
    margin: 0 auto;
    list-style: none;
    padding: 15px;
    background-color: #353535;
    justify-content: center;
}
.Header__Menu ul li {
padding: 0 20px;
font-size: 25px;
font-weight: 300;
}
.Header__Menu ul li a {
text-decoration: none;
color: #fff;
transition: 0.2s;
}
.Header__Menu ul li a:hover {
color: #ff006c;
}